body {
  font-family: sans-serif;
  margin: 20px;
  padding: 0;
}

.IPAChart tr,
.IPAChart td,
.IPAChart {
    border: 1px solid #000000;
    border-collapse: collapse;
    padding: 10px;
    text-align: center;
}

.IPAEntry {
    max-height: 0px;
    max-width: 0px;
    min-width: 12px;
    min-height: 14px;
}
