.base{
    background-color: #fdf6e3;
}
.lower{
    background-color: #cb4b16;
}
.raise{
    background-color: #268bd2;
}
.slash{
    background-color: #6c71c4;
}
.retroflex{
    background-color: #b58900;
}
.implosive{
    background-color: #2aa198;
}

.key-tooltip{
    display: flex;
}

.key {
    background-color: #f0f0f0;
    border: 1px solid #000000;
    border-radius: 4px;
    font-family: Courier New;
    padding: 4px;
    min-height: 28px;
    min-width: 28px;
    max-height: 28px;
    max-width: 28px;
    margin: auto;
    margin-top: 2px;
    text-align: center;
}

.key-background {
    background-color: #787878;
    border-radius: 4px;
    margin:-3px;
    min-height: 32px;
    min-width: 40px;
    max-height: 32px;
    max-width: 40px;
}
